/* About New Page Styles */
.about-hero {
  background: #F5EEE4;
  padding: 6rem 0;
  position: relative;
}

.hero-content {
  text-align: center;
}

.hero-title {
  font-size: 3rem !important;
  font-weight: 700 !important;
  color: #2C3E50 !important;
  margin-bottom: 1.5rem !important;
  line-height: 1.2 !important;
}

.hero-subtitle {
  font-size: 1.2rem !important;
  color: #5A6C7D !important;
  font-weight: 400 !important;
  line-height: 1.6 !important;
  margin-bottom: 3rem !important;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.stats-grid {
  margin-top: 3rem;
}

.stat-item {
  text-align: center;
  padding: 1rem;
}

.stat-number {
  font-size: 2.5rem !important;
  font-weight: 700 !important;
  color: #2C3E50 !important;
  margin-bottom: 0.5rem !important;
}

.stat-label {
  font-size: 1rem !important;
  color: #5A6C7D !important;
  font-weight: 500 !important;
}

/* Foundation Section */
.foundation-section {
  background: white;
  padding: 6rem 0;
}

.section-header {
  display: flex;
  flex-direction: column;
  align-items: center; /* centers horizontally */
  justify-content: center; /* optional if you want vertical centering too */
  text-align: center;
  margin-bottom: 2rem; /* ensures text itself is centered */
}

.section-title {
  font-size: 2.5rem !important;
  font-weight: 700 !important;
  color: #2C3E50 !important;
  margin-bottom: 1rem !important;
}

.section-subtitle {
  font-size: 1.1rem !important;
  color: #5A6C7D !important;
  line-height: 1.6 !important;
  max-width: 600px;
  margin: 0 auto;
}

.foundation-card {
  height: 100%;
  border-radius: 15px !important;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1) !important;
  transition: all 0.3s ease !important;
  border: 1px solid #E8F4F8 !important;
  padding: 2rem !important;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.foundation-card:hover {
  transform: translateY(-5px) !important;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15) !important;
}

.foundation-icon {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 1.5rem auto;
  font-size: 1.5rem;
  color: white;
}

.foundation-icon.orange {
  background: #FF6B35;
}

.foundation-icon.purple {
  background: #8B5CF6;
}

.concentric-circles {
  width: 30px;
  height: 30px;
  border: 3px solid white;
  border-radius: 50%;
  position: relative;
}

.concentric-circles::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 15px;
  height: 15px;
  border: 2px solid white;
  border-radius: 50%;
}

.foundation-title {
  font-size: 1.5rem !important;
  font-weight: 600 !important;
  color: #2C3E50 !important;
  margin-bottom: 1rem !important;
  text-align: center;
  position: relative;
}

.foundation-title::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 40px;
  height: 3px;
  border-radius: 2px;
}

.foundation-card:first-child .foundation-title::after {
  background: #FF6B35;
}

.foundation-card:last-child .foundation-title::after {
  background: linear-gradient(90deg, #8B5CF6, #10B981);
}

.foundation-description {
  color: #5A6C7D !important;
  line-height: 1.6 !important;
  font-size: 1rem !important;
  text-align: center;
  margin-top: 1.5rem;
}

/* Core Values Section */
.core-values-section {
  background: white;
  padding: 6rem 0;
}

.core-values-container {
  background: white;
  border: 1px solid #E0E0E0;
  border-radius: 15px;
  padding: 3rem;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  max-width: 1200px;
  margin: 0 auto;
}

.core-values-header {
  text-align: center;
  margin-bottom: 3rem;
}

.core-values-header .section-subtitle {
  text-align: center;
  margin: 0 auto;
}

.core-values-cards {
  display: flex;
  flex-direction: row;
  gap: 2rem;
  justify-content: center;
  align-items: flex-start;
}

.core-value-card {
  flex: 1;
  text-align: center;
  padding: 1rem;
  background: transparent;
  border: none;
  box-shadow: none;
}

.core-value-card:hover {
  transform: none;
  box-shadow: none;
}

.core-value-icon {
  font-size: 2rem !important;
  margin-bottom: 1rem;
  width: 60px;
  height: 60px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 1rem auto;
}

.core-value-icon.purple {
  background: #EBE7FC !important;
  color: #8B5CF6 !important;
}

.core-value-icon.orange {
  background: #FCECDD !important;
  color: #FF6B35 !important;
}

.core-value-icon.blue {
  background: #E0F4F7 !important;
  color: #3B82F6 !important;
}

.core-value-title {
  font-size: 1.1rem !important;
  font-weight: 700 !important;
  color: #2C3E50 !important;
  margin-bottom: 0.5rem !important;
}

.core-value-description {
  color: #5A6C7D !important;
  line-height: 1.5 !important;
  font-size: 0.9rem !important;
  text-align: center;
}

/* Why Choose Section */
.why-choose-section {
  background: #F5EEE4;
  padding: 6rem 0;
}

.why-choose-card {
  height: 70%;
  border-radius: 15px !important;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1) !important;
  transition: all 0.3s ease !important;
  border: 1px solid #F5EEE4!important;
  text-align: center;
  padding: 2rem !important;
  /* background: white !important; */
}

.why-choose-card:hover {
  transform: translateY(-5px) !important;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15) !important;
}

.why-choose-icon {
  font-size: 2.5rem !important;
  margin-bottom: 1rem;
}

.why-choose-icon.purple {
  color: #8B5CF6 !important;
}

.why-choose-icon.orange {
  color: #FF6B35 !important;
}

.why-choose-icon.blue {
  color: #3B82F6 !important;
}

.why-choose-title {
  font-size: 1.2rem !important;
  font-weight: 600 !important;
  color: #2C3E50 !important;
  margin-bottom: 0.5rem !important;
}

.why-choose-description {
  color: #5A6C7D !important;
  line-height: 1.6 !important;
  font-size: 0.9rem !important;
}

/* Story Section */
.story-section {
  background: white;
  padding: 6rem 0;
}

.timeline {
  position: relative;
  max-width: 800px;
  margin: 0 auto;
}

.timeline::before {
  content: '';
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 4px;
  background: #FF6B35;
  transform: translateX(-50%);
}

.timeline-item {
  position: relative;
  margin-bottom: 3rem;
  width: 50%;
}

.timeline-item:nth-child(odd) {
  left: 0;
  padding-right: 2rem;
}

.timeline-item:nth-child(even) {
  left: 50%;
  padding-left: 2rem;
}

.timeline-content {
  background: white;
  border-radius: 15px;
  padding: 2rem;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  border: 1px solid #E8F4F8;
}

.timeline-year {
  color: #FF6B35 !important;
  font-weight: 700 !important;
  font-size: 1.1rem !important;
  margin-bottom: 0.5rem !important;
}

.timeline-title {
  color: #2C3E50 !important;
  font-weight: 600 !important;
  font-size: 1.2rem !important;
  margin-bottom: 0.5rem !important;
}

.timeline-description {
  color: #5A6C7D !important;
  line-height: 1.6 !important;
  font-size: 0.9rem !important;
}

/* CTA Section */
.cta-section {
  background: #70A6B2;
  padding: 4rem 0;
  text-align: center;
}

.cta-content {
  max-width: 800px;
  margin: 0 auto;
}

.cta-text {
  color: white !important;
  font-weight: 500 !important;
  margin-bottom: 2rem !important;
  line-height: 1.6 !important;
}

.cta-button {
  background: white !important;
  color: #70A6B2 !important;
  font-weight: 600 !important;
  padding: 12px 30px !important;
  border-radius: 25px !important;
  text-transform: none !important;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1) !important;
  transition: all 0.3s ease !important;
}

.cta-button:hover {
  background: #F8F9FA !important;
  transform: translateY(-2px) !important;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15) !important;
}

/* Team Section */
.team-section {
  background: white;
  padding: 6rem 0;
}

.team-card {
  text-align: center;
  border-radius: 15px !important;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1) !important;
  transition: all 0.3s ease !important;
  height: 100%;
  background: white !important;
}

.team-card:hover {
  transform: translateY(-5px) !important;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15) !important;
}

.team-image-container {
  position: relative;
  display: inline-block;
  margin-bottom: 1rem;
}

.team-avatar {
  width: 100px !important;
  height: 100px !important;
  border: 3px solid #E8F4F8;
}

.expert-badge {
  position: absolute !important;
  top: -5px;
  right: -5px;
  background: #FF6B35 !important;
  color: white !important;
  font-size: 0.7rem !important;
  font-weight: 600 !important;
}

.team-name {
  color: #2C3E50 !important;
  font-weight: 600 !important;
  margin-bottom: 0.5rem !important;
  font-size: 1.1rem !important;
}

.team-title {
  color: #70A6B2 !important;
  font-weight: 500 !important;
  font-size: 0.9rem !important;
}

/* Responsive Design */
@media (max-width: 768px) {
  .hero-title {
    font-size: 2.2rem !important;
  }
  
  .hero-subtitle {
    font-size: 1.1rem !important;
  }
  
  .section-title {
    font-size: 2rem !important;
  }
  
  .timeline::before {
    left: 20px;
  }
  
  .timeline-item {
    width: 100%;
    left: 0 !important;
    padding-left: 3rem !important;
    padding-right: 0 !important;
  }
  
  .timeline-item::before {
    content: '';
    position: absolute;
    left: 12px;
    top: 0;
    width: 16px;
    height: 16px;
    background: #FF6B35;
    border-radius: 50%;
    border: 4px solid white;
    box-shadow: 0 0 0 2px #FF6B35;
  }
  
  .foundation-section,
  .core-values-section,
  .why-choose-section,
  .story-section,
  .team-section {
    padding: 4rem 0;
  }
  
  .foundation-card,
  .core-value-card,
  .why-choose-card {
    padding: 1.5rem !important;
  }
  
  
  .core-values-cards {
    flex-direction: column;
    gap: 1rem;
  }
  
  .core-values-container {
    padding: 2rem;
  }
  
  .timeline-content {
    padding: 1.5rem;
  }
}

@media (max-width: 480px) {
  .about-hero {
    padding: 4rem 0;
  }
  
  .hero-title {
    font-size: 1.8rem !important;
  }
  
  .section-title {
    font-size: 1.5rem !important;
  }
  
  .foundation-section,
  .core-values-section,
  .why-choose-section,
  .story-section,
  .team-section {
    padding: 3rem 0;
  }
  
  .foundation-card,
  .core-value-card,
  .why-choose-card {
    padding: 1rem !important;
  }
  
  
  .timeline-content {
    padding: 1rem;
  }
  
  .cta-section {
    padding: 3rem 0;
  }
  
  .cta-text {
    font-size: 1.1rem !important;
  }
}
